.events-header {
  background-color: var(--header-bg);
  padding: 30px 40px;
  color: #333;
}

.events-header__logo {
  height: 45px;
}

.events-header__menu {
  position: relative;
}

.events-header__nav {
  position: absolute;
  right: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  padding: 2em;
  background-color: #f9f9f9;
}

.events-header__nav a {
  color: var(--default-color-text);
  margin-bottom: 0.7em;
}




